<template>
	<view class="container">
		<view class="idCardFront">
			<view class="idCard-img" @click='chooseIdCardImg' data-type="idCardFront">
				<image :src="idCardFront"></image>
			</view>
			<view class="idCard-title">身份证正面</view>
			<view calss="idCard-desc">拍照/上传您的身份证人面像</view>
		</view>
		<view class="idCardBack">
			<view class="idCard-img" @click='chooseIdCardImg' data-type="idCardBack">
				<image :src="idCardBack"></image>
			</view>
			<view class="idCard-title">身份证反面</view>
			<view calss="idCard-desc">拍照/上传您的身份证国徽项</view>
		</view>
		<view class="buttom-next">
			<view class="">
				<u-button text="下一步" color="#FE542C" @click="next"></u-button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				idCardFrontChoose: true,
				idCardBackChoose: true,
				idCardFront: "",
				idCardBack: ""
			}
		},

		created() {
			this.idCardFront = require('../../static/idCard1.png');
			this.idCardBack = require('../../static/idCard2.png')
		},

		methods: {
			// 拍照上传身份证正面
			chooseIdCardImg(e) {
				let type = e.currentTarget.dataset.type;
				if (type === 'idCardFront' && !this.idCardFrontChoose) {
					return false;
				} else if (type === 'idCardBack' && !this.idCardBackChoose) {
					return false;
				}
				this.chooseuniImage(type);
			},
			getExtension: function(path) {
				let _type = '';
				let parts = path.split('.');
				if (path.lastIndexOf('.') >= 0) {
					_type = parts.slice(-1)[0]
				}
				return _type
			},
			chooseuniImage: function(uploadtype) { // 绘制图片到canvas上
				let ctx = uni.createCanvasContext(uploadtype); //画布
				let that = this;
				uni.chooseMedia({ //上传图片事件
					count: 1, //数量
					sizeType: ['compressed'], //original 原图，compressed 压缩图，默认二者都有
					sourceType: ['album', 'camera'], //album 从相册选图，camera 使用相机，默认二者都有
					success: function(result) {
						let ext, filepath = result.tempFilePaths[0],
							file = result.tempFiles[0];
						ext = that.getExtension(filepath).toLowerCase();
						if (!(ext in {
								png: 1,
								jpeg: 1,
								jpg: 1
							})) {
							return uni.showModal({
								title: '出错啦',
								content: '只允许上传png,jpeg,jpg文件'
							})
						} else if (file.size / 1024 > 1024) {
							return uni.showModal({
								title: '出错啦',
								content: '图片文件不能超过1M'
							})
						} else {
							// 将上传的文件转换为base64格式,再请求百度AI进行身份证文字识别
							let fs = uni.getFileSystemManager();
							fs.readFile({
								filePath: filepath,
								encoding: 'base64',
								success: function(data) {
									let params = {
										img64: 'data:image/' + ext + ';base64,' + data.data,
										type: uploadtype
									};
									uni.$u.http.post('user/uploadAttachment', params, function(res) {
										if (res.code === 1) {
											if (uploadtype == 'idCardFront') {
												that.idCardFrontChoose = false;
												that.idCardBackChoose = that.idCardBackChoose;
												that.idCardFront = filepath;
												that.idCardBack = that.idCardBack
											} else {
												that.idCardFrontChoose = that.idCardFrontChoose;
												that.idCardBackChoose = false;
												that.idCardFront = that.data.idCardFront;
												that.idCardBack = filepath
											}
										} else {
											// app.checkCode(res);
										}
									});
								},
								error(e) {
									uni.showToast({
										title: '照片解析错误',
										icon: 'none',
										duration: 1000
									})
								}
							})
						}
					},
					error(e) {
						uni.showToast({
							title: '上传失败',
							icon: 'none',
							duration: 1000
						})
					}
				})
			},

			// 识别成功，点击下一步
			next() {
				uni.navigateTo({
					url: '/packages/authenticationName/authenticationName'
				})
				let that = this;
				if (this.idCardBackChoose || this.idCardFrontChoose) {
					return false;
				};
				uni.$u.http.POST('user/authIdCard', function(res) {
					if (res.code === 1) {
						uni.navigateTo({ // 进行跳转
							url: ''
						})
					} else {
						// app.checkCode(res);
					}
				});
			}
		}
	}
</script>

<style scoped lang="scss">
	.container {
		// background-color: #fff;
	}

	.idCardFront,
	.idCardBack,
	.buttom-next {
		width: 80%;
		margin: auto;
		text-align: center;
		margin-top: 30rpx;
	}

	.idCard-title {
		font-size: 30rpx;
		font-weight: bold;
		margin-top: 30rpx;
	}

	.idCard-desc {
		color: gray;
	}

	.idCard-img {
		width: 90%;
		margin: 0 auto;
		// border: dashed 1rpx #e7e7eb;
	}

	.idCard-img image {
		width: 96%;
		height: 310rpx;
		margin: 10rpx;
	}

	.buttom-next {
		font-size: 25rpx;
		position: fixed;
		z-index: 1;
		bottom: 0;
		display: flex;
		width: 100%;
		justify-content: center;
		filter: alpha(Opacity=60);
		view {
			width: 100%;
			padding: 36rpx;
		}
		
	}
</style>